<template>
  <div id="app">
    <keep-alive>
        <router-view/>      
    </keep-alive>
    <tab class="_tab"></tab>
  </div>
</template>

<script>
import tab from "@/base/tab/tab";
export default {
  name: "App",
  components: {
    tab
  }
};
</script>

<style lang='less'>
#app {
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  width: 100%;
  height: 100%;
}
</style>



