import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import About from '@/components/about'
import Story from '@/components/story'
import Honey from '@/components/honey'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      meta:"没有一点点防备",
      name: 'index',
      component: Index
    },
    {
      path:'/index',
      redirect:'/'
    },
    {
      path: '/about',
      name: 'about',
      meta:"出现在我的世界里",
      component: About
    },
    {
      meta:"我一直等候的那个人",
      path: '/story',
      name: 'story',
      component: Story
    },
    {
      meta:"就是你",
      path: '/honey',
      name: 'honey',
      component: Honey
    },
    {
      meta:"一生中最爱的--你",
      path: '/detail',
      name: 'detail',
      component: Honey
    },
  ]
})



